'use strict';

// CLICK OUTSIDE

document.addEventListener('click', function (e) {
    var target = e.target || e.srcElement;
    var parents = document.querySelectorAll('.selected-item');
    var list = document.querySelectorAll('.select-custom ul');

    for (var i = 0; i < parents.length; i++) {
        if (!parents[i].contains(target)) {
            list[i].className = "";
        }
    }
}, false);

var selectCustom = document.querySelectorAll('.select-custom');

for (var i = 0; i < selectCustom.length; i++) {
    (function (i) {
        var textZone = selectCustom[i].querySelector('.value');
        var selectOriginal = selectCustom[i].parentElement.querySelector('select');
        var selectList = selectCustom[i].querySelector('ul');

        // CUSTOM SELECT SEEDING
        var text = "";
        for (var j = 0; j < selectOriginal.options.length; j++) {
            text += '<li data-id="' + j + '">' + selectOriginal.options[j].text + '</li>';
        }
        selectList.innerHTML = text;

        // CUSTOM SELECT LIST CLICK
        var selectListElement = selectCustom[i].querySelectorAll('li');

        for (var k = 0; k < selectListElement.length; k++) {
            selectListElement[k].addEventListener('click', function (e) {
                e.preventDefault();

                for (var j = 0; j < selectOriginal.options.length; j++) {
                    if (j == this.dataset.id) {
                        selectOriginal.options[j].selected = true;
                    } else {
                        selectOriginal.options[j].selected = false;
                    }
                }

                textZone.innerHTML = this.innerHTML;
                selectOriginal.dispatchEvent(new Event('change'));
            });
        }

        // SELECT OPENING
        selectCustom[i].addEventListener('click', function (e) {
            e.preventDefault();
            selectList.classList.toggle('active');
        });
    })(i);
}
'use strict';

var shirtContainer = document.querySelector('#shirt-container');
var zoom = ['fabric', 'collar', 'wrists', 'pockets', 'bottom', 'back', 'buttons', 'embroidery', 'lining'];

var dataId = null;

function isMobile(e) {
    return !(e.offsetWidth || e.offsetHeight || e.getClientRects().length);
}

// PRELOADING
['bas-hover.png', 'boutons-hover.png', 'broderies-hover.png', 'cols-hover.png', 'dos-hover.png', 'doublures-hover.png', 'poches-hover.png', 'poignets-hover.png', 'tissus-hover.png'].forEach(function (v) {
    return new Image().src = '/img/icons/' + v;
});

document.querySelector('button.back').addEventListener('click', function (e) {
    document.querySelectorAll('.step').forEach(function (v) {
        return v.classList.add('hidden');
    });
    document.querySelector('#step-0').classList.remove('hidden');

    document.querySelector('nav').classList.add('hidden');
    document.querySelector('button.back').classList.add('hidden');
});

document.querySelectorAll('#configurator nav li, #step-0 .part').forEach(function (v) {

    v.addEventListener('click', function (_) {

        dataId = parseInt(v.getAttribute('data-id'));

        document.querySelector('nav').classList.remove('hidden');
        document.querySelector('button.back').classList.remove('hidden');
        document.querySelector('#step-0').classList.add('hidden');
        document.querySelector('#step-' + dataId).classList.remove('hidden');

        document.querySelectorAll('#configurator nav li').forEach(function (v) {
            return v.classList.remove('active');
        });

        document.querySelector('.item-' + dataId).classList.add('active');
        document.querySelectorAll('.step').forEach(function (v) {
            return v.classList.add('hidden');
        });

        document.querySelector('#step-' + dataId).classList.remove('hidden');

        shirtContainer.className = '';
        shirtContainer.classList.add('img-' + zoom[dataId - 1]);

        /*if (isMobile(document.querySelector('#configurator footer'))) {
         document.querySelector('#configurator .step').style.display = "block";
         }*/
    });
});

document.querySelectorAll('#configurator .content .close').forEach(function (v) {
    return v.addEventListener('click', function (e) {
        document.querySelectorAll('#configurator .step').forEach(function (v) {
            return v.classList.add('hidden');
        });
        document.querySelector('#step-0').classList.remove('hidden');
    });
});
'use strict';

function changeFabric(body) {
    fetch('/findAll', {
        method: 'POST',
        body: body
    }).then(function (response) {
        return response.json();
    }).then(function (data) {
        var inject = '';
        if (data.value) {
            data.value.forEach(function (v) {
                inject += '<div class="fabric" data-category="fabric" data-id="' + v.code + '" data-ref="' + v.reference + '">\n                            <img src="http://www.ls-chemise.com/tissu/150/' + v.code + '.jpg"/>\n                            <div class="text">\n                                <span class="name">' + v.reference + '</span>\n                                <span class="price">' + v.prix + '\u20AC</span>\n                                <span class="desc">' + v.construction + '</span></div>\n                        </div>';
            });
        }

        document.querySelector('#fabric-number span').innerHTML = data.value ? data.value.length : 0;
        document.querySelector('#fabrics-container').innerHTML = inject;

        document.querySelectorAll('.fabric').forEach(function (v) {
            return v.addEventListener('click', function (_) {
                shirt.fabricID = v.getAttribute('data-id');
                shirt.fabric = v.getAttribute('data-ref');
                shirt.setElement('fabric');

                document.querySelectorAll('.fabric').forEach(function (v) {
                    return v.classList.remove('active');
                });
                v.classList.add('active');
            });
        });
    });
}

document.querySelectorAll('select').forEach(function (v) {
    return v.addEventListener('change', function (_) {
        return changeFabric(new FormData(document.querySelector('form')));
    });
});

// INIT
var f = new FormData();
f.append('couleur', 'tous');
f.append('motif', 'tous');
f.append('tissage', 'tous');

changeFabric(f);
'use strict';

// ACTIVE ELEMENTS

['collar', 'wrists', 'pockets', 'bottom', 'back', 'buttons', 'embroidery', 'lining', 'epaulettes', 'throat', 'clamp', 'fabric'].forEach(function (v) {
    if (sessionStorage.getItem(v)) {
        document.querySelectorAll('.part[data-category="' + v + '"]').forEach(function (v) {
            return v.classList.remove('active');
        });

        var el = document.querySelector('.part[data-category="' + v + '"][data-id="' + sessionStorage.getItem(v) + '"]');

        if (el) {
            el.classList.add('active');
        }
    }
});

// INIT

var shirt = new ShirtImage();

// ONCHANGE

document.querySelectorAll('.part').forEach(function (v) {
    return v.addEventListener('click', function (_) {
        var cat = v.getAttribute('data-category');
        var id = v.getAttribute('data-id');
        var label = v.getAttribute('data-label');

        document.querySelectorAll('.part[data-category="' + cat + '"]').forEach(function (v) {
            return v.classList.remove('active');
        });

        if (cat != 'summary') {
            v.classList.toggle('active');
            shirt[cat] = id;
            shirt.setLabel(cat, label);
            shirt.setElement(cat);

            if (cat != 'buttonholes') {
                if (cat == 'buttons') {
                    shirt.setPartSummary(cat, 'png');
                } else {
                    shirt.setPartSummary(cat);
                }
            }
        }
    });
});